<template>
  <div class="course-power">
    <head-top>
      <span class="header_title" slot>
        <span>班级二维码</span>
        <!-- 放在头部，因为fixed，导致弹窗显示异常，故采用点击触发方式-->
        <van-icon name="qr-invalid" class="cell_icon" @click.stop.prevent="toShow"></van-icon>
      </span>
    </head-top>

    <div :style="{width: '0', height:'0', overflow: 'hidden'}">
      <r-qrcode ref="erCode" :codeValue="classCode" title="邀请码" sub-title="睿课学首页右上角输入">
        <div class="code_operate" slot="footer">
          班级：
          <span @click="editClass">
                {{className}}
                <van-icon name="edit"></van-icon>
              </span>
        </div>
      </r-qrcode>
    </div>

    <div class="black_board">
      <div class="board_title">{{courseInfo.classHourName}}</div>
      <!--        <div class="sub_title">#{{courseInfo['CourseName']}}</div>-->
      <div class="flex-evenly">
        <span>老师：{{courseInfo.teacherName}}</span>
        <span>班级：{{courseInfo.className}}</span>
      </div>
      <div class="flex-evenly bg_foot">
        <span>教室：{{courseInfo.classRoomName}}</span>
        <span>
            日期：{{ courseInfo.classDate }}
        </span>
      </div>
    </div>

    <div class="sub_nav">
      <span>课堂交互</span>
    </div>

    <div class="flex-wrap-around func_list">
      <div class="func_card" v-for="(item, index) in activitys" :key="index" @click="funcClick(item)"
           :class="{disabled: item.operate === 'screen' && screenLock || item.operate === 'question' && questionLock}">
        <div class="icon activity">
          <i class="iconfont" :class="item.icon"></i>
        </div>
        <div class="card_info">{{item.title}}</div>
      </div>
      <div class="func_card" v-for="sub in (4 - activitys.length % 4) % 4" :key="-sub"></div>
    </div>

    <div class="sub_nav">
      <span>资源</span>
    </div>
    <div class="flex-wrap-around func_list">
      <div class="func_card" v-for="(item, index) in contents" :key="index" @click="contentClick(item)">
        <div class="icon content">
          <i class="iconfont" :class="item.icon"></i>
        </div>
        <div class="card_info">{{item.title}}</div>
      </div>
      <div class="func_card" v-for="sub in (4 - contents.length % 4 ) % 4" :key="-sub"></div>
    </div>
  </div>
</template>

<script>
import RQrcode from "@/components/r-qrcode";
import { formatDate } from "@/utils/formatData";
import { switchType } from "@/utils/enum";

export default {
  name: "course-power",
  components: { RQrcode },
  data () {
    return {
      courseInfo: {
        classHourName: this.$store.getters.classHour.name || "课时名称",
        teacherName: this.$store.getters.teacher["TeacherName"] || '教师名称',
        className: this.$store.getters.class.name || "默认班级",
        classRoomName: this.$store.getters.course.classRoomName || "默认教室",
        classDate: formatDate(this.$store.getters.course.classDate, "yyyy-MM-dd"),
        updateTime: formatDate(this.$store.getters.course.updateTime, "yyyy-MM-dd hh:mm")
      },
      className: this.$store.getters.class.name || "默认班级",
      classCode: this.$store.getters.class.code || "",

      screenLock: this.$store.getters.screenState !== 1, // 是否禁止投屏，默认禁止
      questionLock: this.$store.getters.questionState !== 1, // 是否禁止提问，默认禁止
      activitys: [
        { icon: "icon-qiandao", title: "签到", operate: "", switchType: switchType.Sign, url: "/course/sign" },
        { icon: "icon-renmai", title: "分组", operate: "", switchType: switchType.Group, url: "/course/group" },
        {
          icon: "icon-taolun",
          title: "分组讨论",
          operate: "",
          switchType: switchType.GroupDiscussion,
          url: "/course/group-discussion"
        },
        { icon: "icon-notebook", title: "小组笔记", operate: "", switchType: switchType.Record, url: "/course/group-note" },
        {
          icon: "icon-suijiyonghu",
          title: "随机挑人",
          operate: "",
          switchType: switchType.RandomPick,
          url: "/course/random"
        },
        {
          icon: "icon-jieping",
          title: "截屏提问",
          operate: "",
          switchType: switchType.Record,
          url: "/course/screen-question"
        },
        {
          icon: "icon-edu-biji",
          title: "随堂练习",
          operate: "",
          switchType: switchType.ClassPractise,
          url: "/course/class-practise"
        },
        {
          icon: "icon-kejian",
          title: "课堂课件",
          operate: "",
          switchType: switchType.CloudDisk,
          url: "/course/course-ware"
        },
        { icon: "icon-lock", title: "禁止投屏", operate: "screen", url: "" },
        { icon: "icon-touping", title: "投屏演示", operate: "", url: "/course/throw-screen" },
        { icon: "icon-jinzhifayan", title: "禁止提问", operate: "question", url: "" },
        { icon: "icon-tiwen2", title: "课堂提问", operate: "", switchType: switchType.Question, url: "/course/question" },
        { icon: "icon-weike1", title: "屏幕广播", operate: "", url: "/course/broadcast" },
        { icon: "icon-fenxiang", title: "板书分享", operate: "", switchType: switchType.Record, url: "/course/board-note" }
      ],
      contents: [
        { icon: "icon-cloud-folder", title: "云盘", switchType: switchType.CloudDisk, url: "/cloud-disk" },
        { icon: "icon-yulan", title: "题库", url: "/practise-library" }
        // { icon: "icon-yulan", title: "预览", url: "/course/course-ware/preview" }
      ],
      activeIndex: -1
    };
  },
  created () {
    this.activitys.splice(8, 1, {
      icon: this.screenLock ? "icon-lock" : "icon-unlock",
      title: this.screenLock ? "禁止投屏" : "允许投屏",
      operate: "screen",
      url: ""
    });

    this.getQuestionState();
    this.getScreenState();
  },
  methods: {
    toShow () {
      console.log(this.$refs['erCode']);
      this.$refs['erCode'].qrCodeClick();
    },
    editClass () {
      this.$router.push("/class/edit/" + this.$store.getters.classID + "/" + this.$store.getters.class.name);
    },
    funcClick (item) {
      if (item.switchType || item.switchType === "0") {
        this.$gatewayHttp.broadcast.switchPage({ function: item.switchType });
      }
      if (item.operate === "screen") {
        if (item.title === "允许投屏") {
          this.setScreenState(item, true);
        } else if (item.title === "禁止投屏") {
          this.setScreenState(item, false);
        } else if (this.screenLock) {
          this.$toast("当前禁止投屏，请先开启投屏！");
        } else if (item.url) {
          this.$router.push(item.url);
        }
      } else if (item.operate === "question") {
        if (item.title === "允许提问") {
          this.setQuestionState(item, true);
        } else if (item.title === "禁止提问") {
          this.setQuestionState(item, false);
        } else if (this.questionLock) {
          this.$toast("当前禁止提问，请先允许提问！");
        } else if (item.url) {
          this.$router.push(item.url);
        }
      } else {
        if (item.url) {
          this.$router.push(item.url);
        }
      }
    },
    contentClick (item) {
      if (item.url) {
        if (item.switchType || item.switchType === "0") {
          this.$gatewayHttp.broadcast.switchPage({ function: item.switchType });
        }
        this.$router.push(item.url);
      }
    },
    getQuestionState () {
      this.$gatewayHttp.question.getState().then(res => {
        if (res.Result) {
          this.questionLock = res["IsAllow"];
          this.activitys.splice(10, 1, {
            icon: this.questionLock ? "icon-jinzhifayan" : "icon-fayan",
            title: this.questionLock ? "禁止提问" : "允许提问",
            operate: "question",
            url: ""
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    setQuestionState (item, isLock) {
      this.$gatewayHttp.question.setState({
        isAllow: isLock
      }).then(res => {
        if (res.Result) {
          item.icon = isLock ? "icon-jinzhifayan" : "icon-fayan";
          item.title = isLock ? "禁止提问" : "允许提问";
          this.questionLock = isLock;
          this.$store.commit("SET_QUESTION_STATE", isLock ? 0 : 1);
          this.$toast("设置成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("设置失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    getScreenState () {
      this.$gatewayHttp.broadcast.getState().then(res => {
        if (res.Result) {
          this.screenLock = res["IsAllow"];
          this.activitys.splice(8, 1, {
            icon: this.screenLock ? "icon-lock" : "icon-unlock",
            title: this.screenLock ? "禁止投屏" : "允许投屏",
            operate: "screen",
            url: ""
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    setScreenState (item, isLock) {
      this.$gatewayHttp.broadcast.setState({
        isAllow: isLock
      }).then(res => {
        if (res.Result) {
          item.icon = isLock ? "icon-lock" : "icon-unlock";
          item.title = isLock ? "禁止投屏" : "允许投屏";
          this.screenLock = isLock;
          this.$store.commit("SET_SCREEN_STATE", isLock ? 0 : 1);
          this.$toast("设置成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("设置失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.course-power {
  min-height: 100%;
  background-color: #fff;
  margin-bottom: 50px;

  .cell_icon {
    font-size: @font-title * 1.4;
    vertical-align: middle;
  }

  .black_board {
    overflow: hidden;
    background: url("../../../assets/bg_kecheng.png") 100% 100% no-repeat;
    background-size: cover;
    width: 100vw;
    min-height: 29vw;
    color: rgba(255, 255, 255, 1);

    span {
      display: inline-block;
    }

    .board_title {
      font-size: @font-info;
      margin: 4vw 0;
    }

    .flex-evenly {
      text-align: left;
      margin-bottom: 6px;

      span {
        &:nth-child(1) {
          font-size: @font-info;
          width: calc(100% - 50vw);
          padding-left: 10vw;
        }

        &:nth-child(2) {
          width: 50vw;
        }
      }
    }

    .bg_foot {
      padding-bottom: 4vw;
    }
  }

  .func_list {
    padding-top: 10px;

    .func_card {
      padding-bottom: 16px;
      width: 25vw;
      text-align: center;

      .icon {
        padding-bottom: 5px;
      }

      .activity {
        color: #3AA0E0;
      }

      .content {
        color: #00CBAB;
      }

      .iconfont {
        font-size: 25px;
      }

      .card_info {
        font-size: @font-info;
      }
    }

    .disabled {
      cursor: not-allowed;
      opacity: 0.5;

      .activity {
        color: #ccc;
      }
    }
  }

}
</style>
