<template>
  <span class="r-qrcode" @click.stop="">
    <van-icon name="qr-invalid" class="cell_icon" @click.stop.prevent="qrCodeClick"></van-icon>
    <!-- 展示班级邀请码 -->
    <van-popup v-model="codeShow" :style="{ maxHeight:'unset'}">
      <div class="invite-code">
        <div class="code-info">
          {{title}}：
          <span class="code-value">{{codeValue}}</span>
        </div>
        <p class="code-remark">{{subTitle}}</p>
        <canvas :id="divName"></canvas>
        <slot v-if="$scopedSlots['footer']" name="footer"></slot>
        <!--        <div v-else class="code_operate">-->
        <!--          班级：&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;-->
        <!--          <span @click="editClass">-->
        <!--            {{inviteClassName}}-->
        <!--            <van-icon name="edit"></van-icon>-->
        <!--          </span>-->
        <!--        </div>-->
      </div>
    </van-popup>
  </span>
</template>
<script>
/****
 * 二维码放大组件
 * gfy
 * 2020-03-05
 *  */
import QRCode from "qrcode";

export default {
  name: "r-qrcode",
  components: {},
  props: {
    title: {
      type: String,
      default: "邀请码"
    },
    codeValue: {
      type: String,
      default: "888888"
    },
    subTitle: {
      type: String,
      default: "睿课学首页右上角输入"
    }
  },
  data () {
    return {
      divName: "QrSign" + new Date().getTime(),
      codeShow: false
    };
  },
  methods: {
    qrCodeClick () {
      // e.stopPropagation();
      const code = this.codeValue;
      this.codeShow = true;
      setTimeout(() => {
        QRCode.toCanvas(
          document.getElementById(this.divName),
          JSON.stringify(code, null, "\t"),
          {
            errorCottectionLevel: "H",
            margin: 1,
            width: 300
          },
          error => {
            if (error) {
              console.log(error);
            }
          }
        );
      }, 0);
    }
  }
};
</script>
